  -webkit-appearance: textfield;
  /* 1 */
  /* 2 */
  box-sizing: content-box; }

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */ }

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
button,
input,
select,
textarea {
  color: #222; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

img {
  vertical-align: middle; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

textarea {
  resize: vertical; }

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0; }

@font-face {
  font-family: 'dogs-theme';
  src: url(/static/media/Spartan-Medium.217d42a9.ttf) format("truetype"); }

@font-face {
  font-family: 'dogs-theme-semi-bold';
  src: url(/static/media/Spartan-SemiBold.e9dfc76a.ttf) format("truetype");
  font-weight: bold; }

.page {
  margin: 2rem; }

.main-header {
  padding: 1rem;
  background: #263238;
  text-align: center;
  font-weight: bold;
  border-bottom: 20px solid #546e7a;
  box-sizing: border-box; }
  .main-header__title {
    font-size: 2.5rem;
    line-height: 160%;
    color: #eceff1; }
  .main-header__aside {
    font-size: 1.5rem;
    color: #cfd8dc; }

.dog-form__select {
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #37474f;
  outline: none;
  color: #37474f;
  height: 3rem;
  line-height: 3rem;
  width: 100%;
  font-size: 1rem;
  margin: 0 0 8px 0;
  padding: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  opacity: 1;
  -webkit-transition: opacity 1s;
  transition: opacity 1s; }
  .dog-form__select--disabled {
    opacity: 0.5;
    cursor: not-allowed;
    pointer-events: none; }

.dog-form__button {
  border: none;
  border-radius: 5px;
  display: inline-block;
  height: 36px;
  line-height: 36px;
  padding: 0 16px;
  text-decoration: none;
  color: #fff;
  background-color: #263238;
  text-align: center;
  letter-spacing: .5px;
  -webkit-transition: .3s ease-out;
  transition: .3s ease-out;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2); }
  .dog-form__button--disabled {
    opacity: .5;
    pointer-events: none; }

.dog-form__button:hover {
  background-color: #37474f;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3); }

.message {
  padding: 1rem;
  font-size: 1rem;
  font-weight: bold;
  box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2); }
  .message--error {
    color: #c62828;
    border: 1px solid #c62828; }
  .message--warning {
    color: #ff9100;
    border: 1px solid #ff9100; }
  .message--info {
    color: #0277bd;
    border: 1px solid #0277bd; }
  .message--success {
    color: #76ff03;
    border: 1px solid #76ff03; }

.view-dogs .dog-select__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-flow: nowrap; }

.view-dogs .dog-select__item {
  margin: 1rem; }

@media (max-width: 600px) {
  .view-dogs .dog-select__container {
    flex-flow: wrap; } }

.view-dogs .container-dog {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start; }

.view-dogs .card-dog {
  width: 300px;
  height: 300px;
  text-align: center;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: .3s ease-out;
  transition: .3s ease-out;
  box-sizing: border-box;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
  background-color: #ffffff; }
  .view-dogs .card-dog:hover {
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
    background-color: #cfd8dc;
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg); }
  .view-dogs .card-dog__preload {
    background: #fff9f9;
    background: -webkit-gradient(linear, left top, right top, from(#fff9f9), color-stop(67%, rgba(236, 236, 236, 0.779149)), to(#f4f4f4));
    background: linear-gradient(90deg, #fff9f9 0%, rgba(236, 236, 236, 0.779149) 67%, #f4f4f4 100%);
    -webkit-transition: .3s ease-out;
    transition: .3s ease-out; }
  .view-dogs .card-dog__figure {
    width: 280px;
    height: 280px;
    overflow: hidden; }
  .view-dogs .card-dog__image--horizontal {
    width: 280px;
    height: auto; }
  .view-dogs .card-dog__image--vertical {
    width: auto;
    height: 280px; }

.dogTheme {
  font-family: "dogs-theme", "sans-serif"; }

